// 5. Status Filter (with disabled options)
const statusOptions = [
  { value: 'all', label: 'All' },
  { value: 'active', label: 'Active' },
  { value: 'inactive', label: 'Inactive' },
  { value: 'pending', label: 'Pending', disabled: true },
];

export function StatusFilter() {
  const [status, setStatus] = useState('all');

  return (
    <ToggleGroup items={statusOptions} value={status} onValueChange={setStatus} fullWidth variant="filled" aria-label="Filter by status" />
  );
}
